var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('p',{staticClass:"firstRow"}),_c('h3',[_vm._v("前言")]),_c('p',[_vm._v("      由國立傳統藝術中心委託中華民國國樂學會所執行的「2018臺灣音樂年鑑編輯計畫(案號：1072217)」，共有14個子題，筆者所負責的是「傳統音樂」領域中的「福佬音樂族群」中的第3子題「福佬歌謠」(包括福佬歌謠與說唱音樂)。")]),_c('p',[_vm._v("      受限於時間、人力、財力，筆者不可能鉅細靡遺、毫無遺珠之憾的將2018年所有在臺灣發生的與「福佬歌謠與說唱音樂」有關的活動，全部紀錄下來，以下僅就筆者所掌握到的活動作觀察，將分成「福佬系歌謠」與「福佬系說唱音樂－唸歌仔」兩部分作敘述。各部分再分成「概述」、「2018年福佬系歌謠之觀察」、「2018福佬系說唱音樂－唸歌仔之觀察」兩部分。")]),_c('p',[_vm._v("      福佬系歌謠「概述」部分，再分成定義、傳統民謠、創作歌謠；「2018年之觀察」部分，再分成活動主題、活動時間、活動地點、活動性質、曲目(傳統民謠、創作歌謠－曲目、作詞者/編詞者/譯者、作曲者/編曲者)、小結。")]),_c('p',[_vm._v("      說唱音樂唸歌仔「概述」部分，再分成定義、種類、題材、曲牌、唱詞、伴奏樂器；「2018年之觀察」再分成活動主題、活動時間、活動地點、團體與個人、活動性質、題材、劇目、曲牌、唱詞、伴奏樂器、小結。最後再作總結。以下所有表格與圖片皆由筆者所製作與提供。")]),_c('h3',[_vm._v("壹、福佬系歌謠")]),_c('p',[_vm._v("      以下分「概述」、「2018年之觀察」兩部分敘述。")]),_c('h4',[_vm._v("一、概述")]),_c('p',[_vm._v("      以下有關福佬系歌謠「概述」部分，再分成定義、傳統民謠、創作歌謠等面向作敘述。")]),_c('b',[_vm._v("(一)定義")]),_c('p',[_vm._v("      福佬系歌謠指的是以狹義臺語(閩南語、福佬話)演唱的歌謠，包括無固定作者的傳統民謠與有固定作者的創作歌謠，而後者的數量不僅遠遠超出前者，還不斷的產生。不論傳統民謠或創作歌謠，都包含了成人演唱的歌謠與兒童演唱的童謠。")]),_c('b',[_vm._v("(二)傳統民謠")]),_c('p',[_vm._v("      「傳統民謠」又有「自然民謠」等不同的別稱，即自然流傳在民間的歌謠。換言之，是一個族群「自然衍生」的歌謠，是經由歷史篩選而為族群所共有的。因為口頭傳承，經長期不斷被重新修飾、詮釋過，故已非一人、一時或一地之作。因此它有一定的「母型」，但卻沒有絕對「正確」的版本。由於經由時空的洗鍊，又要能為多數人所唱，因此具有素樸、凝鍊、簡單卻又豐富、真誠、深遠的特質，沒有固定的作者。")]),_c('p',[_vm._v("      臺灣福佬系傳統民謠，依分布地區分類，一般將其分為西部平原(彰南地區)、恆春地區、北宜地區等地。以下2018年的觀察，將分成恆春地區以及恆春地區以外的觀察兩部分作敘述。")]),_c('b',[_vm._v("(三)創作歌謠")]),_c('p',[_vm._v("      「創作歌謠」屬個人創作的歌謠，有固定的作者，有時甚至連第一位主唱者都會被紀錄下來。其與傳統民謠最大的差別，在於後者具有創作過程集體性、詞曲異變性、傳播方式口頭性、具鮮明的民族風格與地方色彩，是經長期時空的考驗、過濾，所流傳下來的經典之作等特徵。")]),_c('p',[_vm._v("      所謂「臺灣福佬系創作歌謠」，即一般所謂的「閩南語創作歌謠」、「臺灣通俗歌謠」或「臺灣歌謠」。「流行歌曲」又稱「城市歌曲」，是創作歌謠的一種，但創作歌謠不一定是流行歌曲。一首創作歌謠能否流傳下來，不能光從流行性來看，因時代性是佔很大因素的。一首創作歌謠若能經得起時代的考驗，必能成為經典之作而長久流傳下去。")]),_c('p',[_vm._v("      臺灣福佬系傳統民謠的量相較於創作歌謠並不多，演唱方式一般民間常見以獨唱、齊唱、對唱等方式呈現；但若在文化場所，如文化中心演藝廳、音樂廳等較具規模的場合演出，則常見以合唱、重唱、音樂劇等方式呈現，甚至跨界演出，伴奏方式也相當多元，有單一樂器、國樂團、管絃樂團、室內樂團等。亦即多已重新編曲，或曲變或詞變或詞曲皆變，已非原始面貌。臺灣第一首詞曲皆由臺灣人自我創作(非改編)的福佬系創作歌曲，一般被認為是產生於1932年的〈桃花泣血記〉，此後八、九十年來所產生的創作歌謠不計其數，且有些作品因具有臺灣人意識、精神、特性、風格等，早已根深蒂固為大家所認同、喜愛，如鄧雨賢的四月望雨(〈四季紅〉、〈月夜愁〉、〈望春風〉、〈雨夜花〉)等，雖這些創作歌謠，有固定作曲者、或作詞者、或詞曲作者，但這些作品已具有強烈臺灣人意識、精神、特性、風格，已完全屬於臺灣人的作品，故本文仍將其列入觀察，已符合本計畫案「傳統音樂－福佬歌謠」之子標題。")]),_c('h4',[_vm._v("二、2018年之觀察")]),_c('p',[_vm._v("      以下有關福佬系歌謠「2018年之觀察」，再分成活動主題、活動時間、活動地點、活動性質、曲目(傳統民謠、創作歌謠－曲目、作詞者/編詞者/譯者、作曲者/編曲者)、小結等面向作敘述。")]),_c('p',[_vm._v("      有關2018年福佬系「歌謠」與「說唱音樂－唸歌仔」之觀察，筆者共掌握到139個活動主題(表1、表9)，「歌謠」66個，「唸歌仔」73個，其中有2個兩邊共有，故實際上為137個(電視臺的重播以及傳習、研習不論時間的長短，皆只算一個主題。但同主題不同地點不同時間，或同主題同地點不同時間的活動，則各算一個主題，因內容不一定完全一樣。)")]),_c('p',[_vm._v("      由於表9中序號12「臺北市內湖區湖元里 湖興里睦鄰互助元宵晚會」、序號15「臺北市松山區五全里樂齡月琴班『音樂發表會』」、序號30「臺北市松山區五全里樂齡月琴班－平時練習」、序號62「擎天關懷長青會月琴傳習活動」，指導教師分別為國寶藝人楊秀卿的藝生鄭美與丁秀津，根據其所言，有說有唱較難，只唱不說較易，為培養學員的學習興趣，上課內容都只以教唱或演唱福佬歌謠為主；另由於月琴是唸歌仔非常重要的伴奏樂器，這些團體皆人手一把月琴自彈自唱，而序號22「臺灣月琴民謠協會『2018北投樂活節』」的演出，也由月琴伴奏，故本文皆將其納入「說唱音樂－唸歌仔」中敘述。")]),_c('p',[_vm._v("      至於序號46「2018(歲次戊戌)雞籠中元祭系列活動－中元音樂晚會」，由於上半場為「古早ㄟ說唱 大家來聽」，下半場為「古早ㄟ民謠 大家來唱」；序號50「2018臺灣月琴民謠祭－『簡單、隨意、心花開』臺灣月琴歌謠音樂會(二)」，演出內容有說唱有歌謠，故「歌謠」與「說唱」兩邊皆呈現。")]),_c('b',[_vm._v("(一)活動主題")]),_c('p',[_vm._v("      2018年有關「福佬系歌謠」的觀察，共有66個活動主題(表1)。表1中「展演」表「展演活動」、「推廣」表「推廣教育」、「傳習」表「傳習活動」、「交流」表「國際與兩岸交流」、「政策」表「文化政策」。")]),_c('span',[_vm._v("表1：2018年福佬系歌謠之活動主題")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","cellpadding":"5"}},[_c('tr',[_c('td',{staticStyle:{"width":"5%"}}),_c('td',{staticClass:"tableCenter"},[_vm._v("活動主題")]),_c('td',{staticClass:"tableCenter",staticStyle:{"width":"15%"}},[_vm._v("日期")]),_c('td',{staticClass:"tableCenter",staticStyle:{"width":"20%"}},[_vm._v("地點")]),_c('td',{staticClass:"tableCenter"},[_vm._v("展演")]),_c('td',{staticClass:"tableCenter"},[_vm._v("推廣")]),_c('td',{staticClass:"tableCenter"},[_vm._v("傳習")]),_c('td',{staticClass:"tableCenter"},[_vm._v("交流")]),_c('td',{staticClass:"tableCenter"},[_vm._v("政策")])]),_c('tr',[_c('td',{staticClass:"tableCenter"},[_vm._v("1")]),_c('td',[_vm._v("追尋歷史．原音重現－民歌採集50年特展")]),_c('td',[_vm._v("2017/10/27