的故鄉〉(文夏詞)、〈媽媽歌星〉(葉俊麟詞)、〈慈母淚痕〉(葉俊麟詞)、〈溫泉鄉的吉他〉(葉俊麟詞)、〈溫泉鄉的故鄉〉(葉俊麟詞)、〈請你信賴我〉(葉俊麟詞)、〈臺灣小調〉、黃昏橋上(葉俊麟詞，越南情歌)等。這些混血歌曲大多是日本曲、葉俊麟詞，曲調雖無臺灣民族意識，但歌詞卻頗具臺灣味，因此也慰藉、擄獲不少臺灣人的心。有少部分取材自臺灣傳統音樂，如〈牛郎織女〉(葉俊麟詞，北管亂彈緊中慢)、〈安童哥買菜〉(葉俊麟詞，歌仔戲雜唸調)等。")]),_c('p',[_vm._v("      而近年來國人作品中，有不少是描寫臺灣歷史、臺灣情(愛情、親情)、臺灣風景(地名、名勝古蹟)、臺灣小吃、臺灣動物等的作品，充滿濃厚的臺灣意識、情懷與風格。如陳維斌的〈放天燈〉描寫新北市平溪、〈暖暖的搖嬰仔歌〉描寫基隆暖暖、〈桐花圓舞曲〉描寫苗栗、〈半月沉江〉描寫臺南安平古堡與赤崁樓、〈飛機雲〉描寫桃園、〈濁水溪溪水濁〉描寫臺灣人、〈花廳門〉描寫臺中霧峰、〈海島戀歌〉描寫屏東墾丁等的作品，比比皆是。")]),_c('span',[_vm._v("(2)作詞者/編詞者/譯者")]),_c('p',[_vm._v("      2018年筆者所觀察到的福佬系創作歌謠的作詞者、編詞者或譯者，至少有73位。(表6)")]),_c('span',[_vm._v("表6：作詞者/編詞者/譯者")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","cellpadding":"5"}},[_c('tr',[_c('td',[_vm._v("小城綾子")]),_c('td',[_vm._v("文夏")]),_c('td',[_vm._v("王明慧")]),_c('td',[_vm._v("王昶雄")]),_c('td',[_vm._v("向陽")]),_c('td',[_vm._v("江中清")])]),_c('tr',[_c('td',[_vm._v("江牧非")]),_c('td',[_vm._v("羊子喬")]),_c('td',[_vm._v("何啟宏")]),_c('td',[_vm._v("吳秀麗")]),_c('td',[_vm._v("吳念真")]),_c('td',[_vm._v("吳景裕")])]),_c('tr',[_c('td',[_vm._v("呂傳梓")]),_c('td',[_vm._v("宋月女")]),_c('td',[_vm._v("宋澤萊")]),_c('td',[_vm._v("李信儀")]),_c('td',[_vm._v("李榮茂")]),_c('td',[_vm._v("李慧玲")])]),_c('tr',[_c('td',[_vm._v("李臨秋")]),_c('td',[_vm._v("那卡諾")]),_c('td',[_vm._v("周添旺")]),_c('td',[_vm._v("林天來")]),_c('td',[_vm._v("林央敏")]),_c('td',[_vm._v("林育伶")])]),_c('tr',[_c('td',[_vm._v("林武憲")]),_c('td',[_vm._v("林強")]),_c('td',[_vm._v("林博鴻（譯）")]),_c('td',[_vm._v("林裕淵")]),_c('td',[_vm._v("林邊")]),_c('td',[_vm._v("武雄")])]),_c('tr',[_c('td',[_vm._v("臥龍（編）")]),_c('td',[_vm._v("施福珍")]),_c('td',[_vm._v("洪志明")]),_c('td',[_vm._v("耶引")]),_c('td',[_vm._v("徐士欽")]),_c('td',[_vm._v("張邱東松")])]),_c('tr',[_c('td',[_vm._v("張景洲")]),_c('td',[_vm._v("張銘耀")]),_c('td',[_vm._v("許丙丁")]),_c('td',[_vm._v("郭文卿")]),_c('td',[_vm._v("陳百潭")]),_c('td',[_vm._v("陳君玉")])]),_c('tr',[_c('td',[_vm._v("陳志生")]),_c('td',[_vm._v("陳坤嶽")]),_c('td',[_vm._v("陳明哲")]),_c('td',[_vm._v("陳明章")]),_c('td',[_vm._v("陳金花")]),_c('td',[_vm._v("陳虹百")])]),_c('tr',[_c('td',[_vm._v("陳達儒")