('td',[_vm._v("陳寶貴")]),_c('td',{staticClass:"tableCenter"},[_vm._v("1")])]),_c('tr',[_c('td',[_vm._v("楊秀卿、鄭美、丁秀津")]),_c('td',{staticClass:"tableCenter"},[_vm._v("1")])])]),_c('br'),_c('b',[_vm._v("(五)活動性質")]),_c('p',[_vm._v("      從表9中知，2018年唸歌仔的活動性質如下(表13)：")]),_c('span',[_vm._v("表13：2018年唸歌仔的活動性質與場次")]),_c('table',{staticStyle:{"border":"1px solid"},attrs:{"rules":"all","cellpadding":"5"}},[_c('tr',[_c('td',{staticClass:"tableCenter"},[_vm._v("性質")]),_c('td',{staticClass:"tableCenter"},[_vm._v("場次")])]),_c('tr',[_c('td',[_vm._v("展演活動")]),_c('td',{staticClass:"tableCenter"},[_vm._v("39")])]),_c('tr',[_c('td',[_vm._v("展演活動與推廣教育")]),_c('td',{staticClass:"tableCenter"},[_vm._v("21")])]),_c('tr',[_c('td',[_vm._v("傳習活動")]),_c('td',{staticClass:"tableCenter"},[_vm._v("4")])]),_c('tr',[_c('td',[_vm._v("推廣教育")]),_c('td',{staticClass:"tableCenter"},[_vm._v("2")])]),_c('tr',[_c('td',[_vm._v("推廣教育與傳習活動")]),_c('td',{staticClass:"tableCenter"},[_vm._v("4")])]),_c('tr',[_c('td',[_vm._v("展演活動、國際與兩岸交流")]),_c('td',{staticClass:"tableCenter"},[_vm._v("2")])]),_c('tr',[_c('td',[_vm._v("展演活動與傳習活動")]),_c('td',{staticClass:"tableCenter"},[_vm._v("1")])])]),_c('br'),_c('p',[_vm._v("      從表13中知，2018年唸歌仔的活動性質以「展演活動」最多，次為「展演活動與推廣教育」。沒有「研究與出版」和「文化政策」。")]),_c('b',[_vm._v("(六)題材")]),_c('p',[_vm._v("      根據筆者2018年的觀察，唸歌仔演出題材主要包括歷史故事、民間傳說、勸世教化、事物、敘情、趣味等類。臺灣歷史故事或民間傳說類者，如《廖添丁傳奇》等；中國歷史故事或民間傳說類者，如「山伯英台」系列、《雪梅教子》等。勸世教化類者如《勸友孝》、《勸世歌》等。事物類者如《臺灣百年歌》、《潭子好風景》等。敘情類者如《翁某相褒》、《探親結緣歌》等。趣味類者如《胡蠅蚊子大戰歌》、《十二生肖貓鼠冤》等。")]),_c('b',[_vm._v("(七)劇目")]),_c('p',[_vm._v("      2018年唸歌仔的演出劇目，可分成傳統劇目、改編傳統劇目、新創劇目三類，其中新創劇目皆微笑唸歌團所創。")]),_c('p',[_vm._v("      傳統劇目有《雪梅教子》(斷機教子)、《十二生肖貓鼠冤》(十二生肖)、《周成過臺灣》、《按君審胡蠅》、《山伯英台》〈母囝答〉、《山伯英台》〈遊花園〉(遊西湖)、《山伯英台》〈四九病相思〉、《山伯英台》〈英台回批〉、《山伯英台》〈山伯討藥方〉、《山伯英台》〈祝英台想欲去杭州讀冊〉、《胡蠅蚊子大戰歌》、《勸世歌》、《勸友孝》、《孟姜女求籤》、《陳三五娘》〈磨鏡〉、《廖添丁》〈打扮客婆〉、《青竹絲奇案》、《探親結緣歌》(姨仔配姊夫)(姨仔姊夫)、《金姑看羊》、《臭頭仔娶貓某》、《螞蟻歌》、《大舜耕田》等。其中除《山伯英台》〈祝英台想欲去杭州讀冊〉為「陳寶貴臺灣唸歌傳承教學計畫」(06/24-09/02)所用之劇目外，其餘皆楊秀卿說唱藝術團與微笑唸歌團等所演出之劇目。")]),_c('p',[_vm._v("      改編傳統劇目有《新編哪吒鬧東海》、《新編廖添丁傳奇》、《新編廖添丁前傳》(新編廖添丁出世)、《翁某相褒》(夫妻相褒)(尪某鬥嘴鼓)、歌仔頭、歌仔尾等。")]),_c('p',[_vm._v("      「歌仔頭」是種開場曲，走江湖的唸歌仔仙會在正式劇目開始演出前，以鑼鼓及高亢的曲調，吸引周遭觀眾前來；或唱或唸吉祥話，祝福國泰民安、觀眾身體健康、一切順利。「歌仔尾」是唸歌仔藝人在演出結束時，演唱感謝觀眾朋友的四句聯，內容大多為感謝觀眾朋友的捧場與支持鼓勵，祝福大家身體健康、萬事如意、財源廣進之類的賀詞，頗具人情味及趣味性。因此演出者在演出「歌仔頭」與「歌仔尾」時，除會保留傳統的演出內容外，也會斟酌活動主題，加入一些符合活動主題的創作內容，因此「歌仔頭」與「歌仔尾」的演出內容可說是「傳統＋隨機創作」。")]),_c('p',[_vm._v("      新創劇目有《白冷圳．我的母親》(劇本原創為劉秀庭)、《故宮×JUST IN XX周裕穎紐約時裝週創作紀實》、《潭子好風景》、《臺灣百年歌》、《大墩美展》、《臺中文學館百萬人次》、《新婚祝賀》、《新年祝賀》、《廚餘分類宣導短片》等，都是為邀請者為了某個活動主題而特別設計、創作的。其中除《新年祝賀》為楊秀卿所創作的之外，其餘皆微笑唸歌團所創。")]),_c('p',[_vm._v("      2018年的觀察，唸歌仔的演出劇目以《雪梅教子》與《十二生肖貓鼠冤》最多，各17次。後依序為《山伯英台》〈母囝答〉15次、《勸世歌》10次、《山伯英台》〈遊花園〉與《翁某相褒》各6次等。而「山伯英台」系列則有最多不同的選段。這些劇目基本上都是最受觀眾喜愛的。")]),_c('b',[_vm._v("(八)曲牌")]),_c('p',[_vm._v("      2018年的觀察，唸歌仔演出所用的曲牌不外乎【江湖調】、【七字調】、【雜唸調】、【都馬調】等，其中以【江湖調】最多，特別是在傳習活動與推廣活動中，【江湖調】可說是首先會被選用的曲牌。")]),_c('p',[_vm._v("      而楊秀卿說唱藝術團「2018年度唸歌大戲－《按君審胡蠅》」巡演，所用到的曲牌則有【江湖調】、【江湖哭】、【七字調】、【都馬調】、【雜唸調】、【倍思仔】、【五更鼓】、【更鼓反】、【藏調仔】、【將水】、【陰調】、【江西調】、【文和調】等，而這些曲牌也都是歌仔戲常用的曲牌。")]),_c('b',[_vm._v("(九)唱詞")]),_c('p',[_vm._v("      2018年的觀察，唸歌仔唱詞皆七字仔形式，亦即皆四句聯，但會隨機增加虛字襯詞，讓音樂更為流暢，表達更為貼切自然，此以楊秀卿最為明顯。至於創作文本向來就是用整齊規則的四句聯形式創作的。")]),_c('b',[_vm._v("(十)伴奏樂器")]),_c('p',[_vm._v("      根據2018年唸歌仔之觀察，其所用的伴奏樂器有月琴、大廣絃、殼仔絃、電吉他、三絃、電子琴、堂鼓、大鑼、小鑼、小鈸等，其中月琴、大廣絃可說是最重要的兩件樂器，而被認為是國民樂器的月琴更是不可或缺，尤其傳習活動與推廣教育中，幾乎是人手一把月琴自彈自唱，或自彈自說自唱。")]),_c('p',[_vm._v("      楊秀卿除以月琴自彈自唱自說外，更常以大廣絃伴奏演出；微笑唸歌團的儲見智習慣以大廣絃自拉自唱自說或搭配月琴、堂鼓演出；陳美珠則慣用電吉他自彈自唱自說或搭配其他樂器演出；旭陽民俗車鼓劇團慣以三絃、大廣絃伴奏；另微笑唸歌團有時會在比較熱鬧或場面較大的場合，增加Keyboard（電子琴），以豐富音響效果。此外，演出「歌仔頭」時，為吸引周遭觀眾前來觀賞，會加上大鑼、小鑼、小鈸等音響較粗獷的打擊樂器。")]),_c('p',[_vm._v("      根據2018/06/30微笑唸歌團在臺灣民俗文物館舉辦的「2018臺灣唸歌節－唸歌大觀園文物特展」中，所展出的伴奏樂器圖片有月琴、大廣絃、殼仔絃、雙清、小揚琴、三絃，但其中雙清、小揚琴筆者在2018年的演出觀察中並未見到。另又如新北市新莊區擎天府擎天關懷長青會月琴傳習、推廣活動中，教師為方便教學，常會以小鈸、小鼓、鈴鼓、木魚等打擊樂器輔助教學，但實際演出時並不會用到這些樂器。傳統上唸歌仔是不用打擊樂器的，但2018年的觀察筆者卻看到了堂鼓、大鑼、小鑼、小鈸等打擊樂器的演出。以下圖為2018年部分實際演出筆者所見到的伴奏樂器。")]),_c('table',{staticStyle:{"border":"1px solid #fff","width":"100%"},attrs:{"rules":"all","cellpadding":"5"}},[_c('tr',[_c('td',[_c('img',{staticStyle:{"max-width":"100%"},attrs:{"src":require("./../../../assets/ob3/image1.jpg"),"alt":""}}),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("臺中市中和國小(01/29)(黃玲玉提供)")])]),_c('td',[_c('img',{staticStyle:{"max-width":"100%"},attrs:{"src":require("./../../../assets/ob3/image2.jpg"),"alt":""}}),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("臺北市北投國小(07/13)(黃玲玉提供)")])])]),_c('tr',[_c('td',[_c('img',{staticStyle:{"max-width":"100%"},attrs:{"src":require("./../../../assets/ob3/image3.jpg"),"alt":""}}),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("臺北市松山區永吉路五全里樂齡月琴班(05/25)"),_c('br'),_vm._v("(黃玲玉提供)")])]),_c('td',[_c('img',{staticStyle:{"max-width":"100%"},attrs:{"src":require("./../../../assets/ob3/image4.jpg"),"alt":""}}),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("新北市新莊區擎天府擎天關懷長青會月琴傳習活動(11/17)(黃玲玉提供)")])])]),_c('tr',[_c('td',[_c('img',{staticStyle:{"max-width":"100%"},attrs:{"src":require("./../../../assets/ob3/image5.jpg"),"alt":""}}),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("臺灣唸歌節(07/08)(黃玲玉提供)")])]),_c('td',[_c('img',{staticStyle:{"max-width":"100%"},attrs:{"src":require("./../../../assets/ob3/image6.png"),"alt":""}}),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("臺灣月琴民謠祭(09/23)(黃玲玉提供)")])])])]),_c('br'),_c('b',[_vm._v("小結")]),_c('p',[_vm._v("      就2018年有關福佬系說唱音樂－唸歌仔的觀察，筆者共觀察了73個活動主題。就活動時間言，有76個場次，以11月份的11次最多。就活動地點言，臺灣除新竹、苗栗、南投、屏東、臺東及離島外，遍及各縣市，而在臺中的演出就佔了50%，此與微笑唸歌團位居臺中的地緣有關，微笑唸歌團的活動地點以臺中為大本營，2018年其所負責的場次就佔約65%左右。就活動性質言，以「展演活動」最多，次為「展演活動與推廣教育」，沒有「研究與出版」和「文化政策」。")]),_c('p',[_vm._v("      就演出團體與個人言，尚存團體只有楊秀卿說唱藝術團、微笑唸歌團、臺灣月琴民謠協會等有常態、實質的演出活動，其中以微笑唸歌團最為活躍，旭陽民俗車鼓劇團偶而會被邀請客串演出。個人方面有國寶級藝人楊秀卿，及其藝生鄭美、丁秀津、儲見智、林恬安、邱思笛、周玉湘、鄭欣蘭等人，前4人都已能獨當一面。而陳美珠、陳寶貴、洪明珠、周定邦、陳明章等人的演出，也都各具特色。")]),_c('p',[_vm._v("      演出題材主要包括歷史故事、民間傳說、勸世教化、事物、敘情、趣味等類。演出劇目可分成傳統劇目、改編傳統劇目、新創劇目三類，其中新創劇目大多微笑唸歌團所創。以《雪梅教子》與《十二生肖貓鼠冤》的演出最多，後依序為《山伯英台》〈母囝答〉、《勸世歌》、《山伯英台》〈遊花園〉(遊西湖)、《翁某相褒》(夫妻相褒)(尪某鬥嘴鼓)等。而「山伯英台」系列則有最多不同的選段被演出。「歌仔頭」與「歌仔尾」的演出，除會保留傳統的內容外，也會斟酌活動主題，加入一些符合活動主題的創作內容，因此「歌仔頭」與「歌仔尾」可說是「傳統＋隨機創作」混合的內容。")]),_c('p',[_vm._v("      所用的曲牌不外乎【江湖調】、【七字調】、【雜唸調】、【都馬調】等，其中以【江湖調】最多，特別是在傳習活動與推廣教育中，【江湖調】可說是首先會被選用的曲牌。而楊秀卿說唱藝術團「2018年度唸歌大戲－《按君審胡蠅》」巡演，所用到的曲牌則有【江湖調】、【江湖哭】、【七字調】、【都馬調】、【雜唸調】、【倍思仔】、【五更鼓】、【更鼓反】、【藏調仔】、【將水】、【陰調】、【江西調】、【文和調】等，而這些曲牌也是歌仔戲常用的曲牌。")]),_c('p',[_vm._v("      唱詞方面皆七字仔形式，亦即皆四句聯，但會隨機增加虛字襯詞，讓音樂更為流暢，表達更為貼切自然，此以楊秀卿最為明顯。至於創作文本向來就是用規則整齊的四句聯形式創作的。")]),_c('p',[_vm._v("      伴奏樂器方面有月琴、大廣絃、殼仔絃、電吉他、三絃、電子琴、堂鼓、大鑼、小鑼、小鈸等，其中月琴、大廣絃可說是最重要的兩件樂器，而被認為是國民樂器的月琴更是不可或缺，尤其傳習活動與推廣教育中，幾乎是人手一把月琴自彈自唱，或自彈自說自唱。")]),_c('h3',[_vm._v("結語")]),_c('p',[_vm._v("      2018年有關傳統音樂「福佬歌謠」與說唱音樂「唸歌仔」的觀察，基本上除了保留傳統外，為了順應時代潮流，或多或少都加入了時代的因素，作了一定程度的改變與創新。傳統不能墨守成規，否則終會消失在時代的洪流中，因此保留傳統中的精華，利用其作適度的改變而能為大家所接受，應是該走的一條路。今日的創新是明日的傳統，因此傳統與創新之間如何取得平衡，是能否永續發展的重要因素。")]),_c('p',[_vm._v("      恆春地區由於佔地利與人和之便，保存了臺灣福佬系民歌中最純樸、最獨特的民歌，但從「2018半島歌謠祭」中的觀察，整個歌謠祭除了保留傳統外，已順應時代潮流，作了創新，跟流行、跟世界作了結合。是結合世界音樂、臺灣民謠、恆春民謠及半島自然地景與社區生態而成的國際藝術節慶。是藉由地方特性，成為在地音樂與國際共創的舞臺。半島歌謠祭中，第12屆屏東縣「恆春民謠全國大賽」，更是恆春地區的全民運動，恆春人已將恆春民謠的推廣自幼兒園開始紮根。")]),_c('p',[_vm._v("      簡上仁希望能培育孩子們「立足本土，放眼國際」的人生觀，建立臺灣的文化與臺灣的主體性，因此對臺灣福佬系歌謠不論是展演、推廣、創作、保存等各方面，都竭盡心力，特別是在童謠方面著墨更深。2018年光簡上仁設計、擔綱、主持、主講的場次就多達35場，約佔51%，超過半數。")]),_c('p',[_vm._v("      說唱音樂「唸歌仔」，就現況言事實上已相當式微，但微笑唸歌團卻在2018年扮演了舉足輕重的腳色，他們克服了經費、家庭、學業等多重的壓力，只為唸歌仔藝術能在夾縫中求生存、永續發展，這應說是時代賦予他們的使命吧！")]),_c('p',[_vm._v("      經過2018年唸歌仔的觀察，以下筆者想為唸歌仔的傳承提出一點小小的看法與建議。有關藝生的培養，目前國寶級唸歌仔藝人僅存楊秀卿一人，楊秀卿已高齡85歲(1934年生)，仍在為唸歌仔藝術的展演、傳承、推廣等竭盡心力。其自11歲始即在茶樓、酒家、廟口等地走唱，藝齡已近75年，臺灣的唸歌仔藝術能保存至今，其功不可沒，而其藝術地位也無人能取代。")]),_c('p',[_vm._v("      約10年前，筆者深感唸歌仔藝術，終將會因後繼無人而走入歷史，但這些年來在藝生制度的培養下，已漸展露成果，陸續培育出鄭美、丁秀津、儲見智、林恬安等藝生。然因現實面的考量，鄭美、丁秀津的傳習活動與推廣教育，為考量學員的學習興趣，目前都只以教唱福佬系歌謠以及月琴彈奏為主，期能培養學員們自彈月琴自唱的自娛能力。至於微笑唸歌團(儲見智、林恬安)以目前情勢來看，接棒楊秀卿者已非他們莫屬，不論保存、出版、展演、推廣、傳習等都不遺餘力，2018年他們負責的場次就佔約65%。然也因現實環境的影響，只能以「硬撐、苦撐」來形容。房租、人事等開銷，在負成長與家庭、學業(儲見智、林恬安目前都還是國立臺南藝術大學民族音樂學研究所的研究生)等壓力下，也不得不曾經思考過何去何從，然在眾多人的鼓勵、期許下，仍扛起此重任繼續堅持下去。在大環境的變遷下，傳統藝術的保存著實不易，期盼政府單位能多予年輕人支持、鼓勵，讓此奄奄一息的傳統藝術，能永續發展。")]),_c('p',[_vm._v("      高齡已85歲的國寶藝人楊秀卿，深恐唸歌仔藝術走入歷史，近年來不斷鼓勵微笑唸歌團要堅持下去，甚至一再叮嚀團長儲見智，只要申請案子，一律以微笑唸歌團的名義申請，不用顧慮到她與楊秀卿說唱藝術團，因她願意只站在背後支援、協助，尤其是2018年更是用心良苦、苦口婆心的再三叮囑，為的只是希望唸歌仔藝術能永續發展，免於失傳。多坦蕩蕩、提攜後輩的心胸啊！")]),_c('p',[_vm._v("      2018年第三屆說唱「唸歌仔」藝生甄選(1/24)，共錄取3位。據了解期中考後陣亡1位，已放棄補考；期末考(11/18)後差點又陣亡2位，經苦勸後才參加補考。臺灣傳統音樂，如歌仔戲、布袋戲、南管、北管等，都還有不少館閣或團體存在，傑出藝人也大有人在，但唸歌仔只有國寶級藝人楊秀卿一人，被指定為「重要無形文化資產」的傳藝師。楊秀卿已高齡85歲，可說在與時間賽跑，加上藝生的甄選與培訓著實不易，在期中考、期末考的標準上，是否一開始不要要求太高，畢竟楊秀卿在唸歌仔領域中已有70多年的歷練，技術上已爐火純青，想要在短短幾個月內就學到楊秀卿的真髓，談何容易，需給她們更多時間的磨練與培訓，否則沒有藝生的培訓，唸歌仔藝術的傳承與發展豈不前途更為坎坷。筆者以為唸歌仔藝術當務之急應要先存活，故應吸引更多人看得到、有興趣、能參與、再入門學習，因此對於藝生的培養應更有耐心，宜給予更多的學習信心和鼓勵。")]),_c('br'),_c('hr'),_c('p'),_c('small',[_vm._v("1.張炫文（1986）。臺灣的說唱音樂。臺灣省政府教育廳交響樂團發行。")]),_c('br'),_c('small',[_vm._v("2.張炫文（1986）。臺灣的說唱音樂。臺灣省政府教育廳交響樂團發行。")])])}]

export { render, staticRenderFns }